*{
    padding: 0;
    margin: 0;
}
.main-con{
    width: 100vw;
    height: 100vh;
    position: fixed;
    background-color: #000000;
    display: flex;
}
.form-con, .data-con{
    min-width: 50%;
    padding: 50px;

}
.form-con{
    background-color: #f3f3f3;
    border-right: 1px solid;
    
}
.data-con{
    height: 100%;
    background-color: #f3f3f3;
    position: sticky;
    right: 0;
    left: 50%;
    overflow-y: auto;
    display: flex;
    flex-direction: column;
}
.data-con h1{
    width: 100%;
    text-align: center;
}

table{
    width: 100%;
    display: flex;
    flex-direction: column;
    background-color: #000000;
}
thead{
    padding: 10px;
    text-transform: uppercase;
    flex: 1 1 20%;
}
th{
    flex: 1 1 20%;
    text-align: center;
    color: #ffffff;
}
tr{
    display: flex;
    width: 100%;
    padding: 10px;
}
tbody tr{
    display: flex;
    width: 100%;
    padding: 10px;
}
tbody tr.even{
    background-color: #b4b4b4;
    
}

tbody{
    flex: 1 1 20%;
    background-color: #ffffff;
}
td{
    flex: 1 1 20%;
    text-align: center;
    color: #000000;
}
table button{
    margin: 0px 5px;
    border: none;
    outline: none;
    background-color: transparent;
    display: inline;
    justify-content: center;
    align-items: center;
}

table button:hover{
    color: red;
}

form{
    padding: 20px;
    display: flex;
    flex-direction: column;
}

form .row{
    margin: 20px 0px;
}
input, select{
    padding: 10px;
}
form button{
    width: 200px !important;
    padding: 10px;
    margin: auto;
}
.image-container{
    width: 100px;
    height: 100px;
    background-color: #ffbcbc;
    display: flex;
    justify-content: center;
    align-items: center;
}
.image-container img{
    width: 100%;
    height: 100%;
    object-fit: cover;

}